
  
  
  p {
    text-shadow: 0 0 7px rgba(255,255,255,.3), 0 0 3px rgba(255,255,255,.3);
  }
    



header {
    height: 100vh;
    padding-top: 1rem;
    overflow: hidden;
    margin-bottom: 5rem;
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-right: 5rem;
    margin-left: 5rem;
    justify-content: center;
   
    
    
}

.leftcontainer {
    display: flex;
    flex-direction: column;
    gap: 1vh;
    align-items: center;
    justify-content: center;
    margin-left: 2.5vw;
    
}


.leftcontainer h1{
    text-align: center;
    font-size: 2.5vw;
    
    text-shadow: 0 0 7px rgba(255,255,255,.3), 0 0 3px rgba(255,255,255,.3);
    
}

.firstanim {
    animation: slideInDown;
    animation-duration: 2s;
    color: var(--color-primary);
    font-size: 2vw;
}

.secondanim {
    animation: slideInLeft;
    animation-duration: 3s;
    font-size: 3vw;
    color: var(--color-light);
}

.thirdanim {
    animation: slideInUp;
    animation-duration: 4s;
    font-size: 2vw;
    color: var(--color-primary);
}

@keyframes slideInDown {
    from {
      transform: translate3d(0, -100%, 0);
      visibility: visible;
      opacity: 0;
    }
  
    to {
      transform: translate3d(0, 0, 0);
      opacity: 1;
    }
  }

  @keyframes slideInUp {
    from {
      transform: translate3d(0, 100%, 0);
      visibility: visible;
      opacity: 0;
    }
  
    to {
      transform: translate3d(0, 0, 0);
      opacity: 1;
    }
  }

  @keyframes slideInLeft {
    from {
      transform: translate3d(-100%, 0, 0);
      visibility: visible;
      opacity: 0;
    }
  
    to {
      transform: translate3d(0, 0, 0);
      opacity: 1;
    }
  }



/* CTA */



.cta {
    display: flex;
    gap: 0.4vw;
    justify-content: center;
    flex-direction:row;
    align-items: center;
    font-size: 1vw;

    

}

/* SOCIALS */

.header__socials {
    display: flex;
    flex-direction: row;
    gap: 0.8vw;
    justify-content: center;
    font-size: 2vw;
    margin-top: 8vh;
}


.header__socials a:hover {
    transform: scale(1.5);
    color: var(--color-primary);
    
}



/* IMAGE */

.me {
    position: relative;
    right: 0;
    top: 0;
    margin: 1vw;
    overflow: hidden;
    padding: 0;
    width: 40vw;
    height: 90vh;
    box-shadow: 8px 8px 24px 0px rgba(47, 62, 70, 1);
    border-radius: 2rem;
    display: grid;
    grid-template-columns: 1fr;
    justify-content: center;
    align-items: center;
    
    
}

.me > img {
    border: 1px solid transparent;
    opacity: 0.5;
    position: absolute;
}

.logo__container {
    width: 20rem;
    height: auto;
    position: absolute;
    top: 0;
    left: 0;
    margin-top: 1rem;
    margin-left: 1rem;
    top: 0;
    display: flex;
    
    
}

.logo__container > img {
    border-radius: 12rem;
}

header::after {
    content: "";
    background: url("../../assets/niebo.png");
    background-size:cover;
    background-repeat: no-repeat;
    opacity: 0.2;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    position: absolute;
    z-index: -1;
}




/* media queries (tablets) */ 

@media screen and (max-width: 1024px) {
    .logo__container{
        width: 10rem;
        margin-top: 0.4rem;
        margin-left: 0.4rem;
    }

    header {
        grid-template-columns: 1fr;
        height: 100vh;

    }

    .me {
        width: 100vw;
        align-self: center;
        opacity: 0.2;
        transform: translateY(-50%);
       }
       
       .me img {
        width: 100%;
        margin-top: 4vh;

}
}
/* media queries (phones) */ 

@media screen and (max-width: 600px) {
   header {
    grid-template-columns: 1fr;
   }

   .me {
    width: 100vw;
    align-self: center;
    margin: auto;
    z-index: -2;
   }
   
   .me img {
    width: 100%;
    margin-top: 4vh;


    
   }

   .leftcontainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0;
   }


}