

nav {
    width: 6vw;
    background: rgba(0, 0, 0, 0.3);
    z-index: 2;
    position: fixed;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    gap: 2vh;
    border-bottom-right-radius: 2vw;
    backdrop-filter: blur(15px);
    left: 0;
    top: 0;
    text-align: center;
    margin-top: 1vh;
    margin-left: 0;
    padding-bottom: 2vh;
    flex-shrink: 1;
    border-left: 1vw solid rgba(0, 0, 0, 0.3);

}

nav img {
    width: 140%;
    border-top-right-radius: 4vw;
    border-bottom-right-radius: 4vw;
}

nav a:hover {
    transform: scale(1.3);
}

nav a.active {
    color: var(--color-primary);
    transform: scale(1.3);
}

.navbtn  {
    background: transparent;
    padding: 10%;
    min-width: 1%;
    font-size: 1rem;
    justify-content: center;
    text-align: center;
    display: flex;
    font-size: 1.5vw;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    
}

.navbtn h2 {
    font-size: 0.8vw;
    text-align: center;
}








.logo__container > img {
    border-radius: 12rem;
}

@media screen and (max-width: 1024px) {
   

}

/* media queries (phones) */ 

@media screen and (max-width: 600px){
    

    

    nav img {
        display: none;

    }
    
    nav{
        width: fit-content;
        height: fit-content;
        border: none;
        border-radius: 2vw;
        padding-top: 2vh;
        padding-bottom: 2vh;
        gap: 2vw;
        padding-left: 0.3vw;
        padding-right: 0.3vw;
        flex-direction: row;
        bottom: 10vh;
        top: 90vh;
        left: 50%;
        transform: translateX(-50%);
        margin-top: 0;
    }
}
  
   