.projects__container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
    background-image: url('../../assets/projects.png');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    box-shadow: 8px 8px 24px 0px rgba(47, 62, 70, 1);
    border-radius: 2rem;
}

.projects__item {
    background: var(--color-primary);
    padding: 0.6rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
    line-height: 0.8rem;
    margin: 5rem;
    opacity: 0.7;
    text-shadow: 0 0 7px rgba(47, 62, 70,.3), 0 0 3px rgba(47, 62, 70,.3);

}

.projects__item:hover {
    transform: scale(1.1);
    opacity: 1;
    box-shadow: 8px 8px 24px 0px rgba(53, 79, 82, 1);
}

.projects__item-image {
    border-radius: 1rem;
    height: 40%;
    overflow: hidden;
}

.projects__item h3 {
    text-align: center;
    margin: 1.2rem 0 1rem;
}

.projects__item h4 {
    text-align: center;
    margin: 1.2rem 0 1rem;
    color: var(--color-bg);
}

.projects__item-buttons {
    display: flex;
    gap: 1rem;
    margin-bottom: 0rem;
    justify-content: center;
}

.projects__item-buttons a {
    border: 1px solid var(--color-light);
}

/* media queries (tablets) */ 

@media screen and (max-width: 1024px) {
    .projects__container {
        grid-template-columns: 1fr 1fr;
        gap: 1.2rem;
    }
      
}

/* media queries (phones) */ 

@media screen and (max-width: 600px) {
    .projects__container {
        grid-template-columns: 1fr;
        gap: 1rem;
    }
    
}