.skills__container {
    display: grid;
    border: 1px solid transparent;
    border-radius: 2rem;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
    background-image: url('../../assets/tech.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    box-shadow: 8px 8px 24px 0px rgba(47, 62, 70, 1);
    margin:auto;
}
    

.skills__container > div {
    /* background-color: var(--color-primary); */
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    margin-left: 3rem;
    margin-top: 3rem;
    margin-bottom: 3rem;
    margin-right: 3rem;
    background-image: url('../../assets/tech.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    box-shadow: 8px 8px 24px 0px rgba(47, 62, 70, 1);
        
}

.skills__container > div:hover {
    background-color: transparent;
    /* border-color: var(--color-primary); */
    transition: var(--transition);
    
}

.skills__container > div h3 {
    text-align: center;
    margin-bottom: 1rem;
    color: var(--color-light);
    
}


.skills__content {
    display: grid;
    grid-template-columns: 1fr;
    row-gap: 2rem;
    text-shadow: 0 0 7px rgba(47, 62, 70,.3), 0 0 3px rgba(47, 62, 70,.3);
    
}

.skills__details {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 0.4rem;
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 0.2rem;
    opacity: 0.7;
    background-color: var(--color-primary);
    
}

.skills__details:hover {
    border-color: var(--color-primary);
    background-color: var(--color-primary);
    transition: var(--transition);
    transform: scale(1.1);
    opacity: 1;
    box-shadow: 8px 8px 24px 0px rgba(53, 79, 82, 1);    

    
}

.skills__details-icon {
    margin-top: 6px;
    color: white;
    font-size: 2rem;
}


ul {
    display: flex;
    flex-direction:row;
    gap: 7rem;
    justify-content: center;
    
}


li > h4 {
    text-align: center;
    color: var(--color-light);
    font-size: 1rem;
}


/* media queries (tablets) */ 

@media screen and (max-width: 1024px) {
    .skills__container {
        grid-template-columns: 1fr;
    }

    .skills__container > div {
        width: 80%;
        padding: 2rem;
        margin: 0 auto;
    }

    .skills__content {
        padding: 1rem;
    }
  
}



/* media queries (phones) */ 

@media screen and (max-width: 600px) {
    .skills__container {
        gap: 1rem;
    }
    
    .skills__container > div{
        width: 100%;
        padding: 2rem 1rem;
    }
    }